:root {
  --space-small: 5%;
  --space-medium: 10%;
  --space-large: 18%;
  --gap-small: 1rem;
  --gap-medium: 2rem;
  --gap-large: 4rem;
  --gap-huge: 8rem;
  --white: #fff;
  --silk: #f5f5f5;
  --silk-shade: #f5f5f580;
  --charcol: #424c4c;
  --charcol-shade: #424c4c85;
  --black: #1c1c1c;
  --icon: 5rem;
  --border-radius: 10px;
  --time: .25s;
  --time-long: 6s;
}

:root, html[data-theme="light"] {
  --background-color: #f5f5f5;
  --border-color: #c7cdd1;
  --button-color: #fff;
  --accent-color: #eeeeee98;
  --line-color: linear-gradient(to top, #424c4c00 0%, #424c4c85 2.5%, #424c4c85 97.5%, #424c4c00 100%);
}

html[data-theme="dark"] {
  --background-color: #1c1c1c;
  --text-color: #f5f5f5;
  --border-color: #323232;
  --button-color: #2c2c2c;
  --accent-color: #2c2c2c98;
  --line-color: linear-gradient(to top, #9db7b700 0%, #9db7b785 2.5%, #9db7b785 97.5%, #9db7b700 100%);
}

*, :before, :after {
  box-sizing: inherit;
}

html * {
  scroll-behavior: smooth;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
