.section {
  padding: var(--gap-huge) 0;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow-x: hidden;
}

.title {
  font-size: 4rem;
  line-height: 4;
}

.subtitle {
  font-size: 2.5rem;
}

.center {
  text-align: center;
}

.tag {
  background: var(--button-color);
  padding: var(--gap-small) var(--gap-medium);
  font-size: 1.5rem;
}

.outlined {
  border: 2px solid var(--charcol-shade);
  transition: border var(--time) ease;
}

.outlined:hover {
  border: 2px solid #fff0;
}

.rounded {
  border-radius: var(--border-radius);
}

.acrylic {
  background-blend-mode: exclusion;
  -webkit-backdrop-filter: blur(24px);
  -webkit-backdrop-filter: blur(24px);
  backdrop-filter: blur(24px);
}

.full-frame {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform var(--time) ease;
  transform: scale(1);
}

@media (min-width: 768px) {
  .full-frame:hover {
    transform: scale(1.05);
  }
}

.button {
  background: var(--button-color);
  transition: box-shadow var(--time) ease;
  display: block;
}

.button:hover, .button:focus {
  background: none;
  animation: 1s pulse;
  box-shadow: 0 0 0 2em #0000;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 var(--charcol-shade);
  }
}

.link {
  background: var(--black);
  padding: var(--gap-medium);
  text-align: center;
}

.faded {
  opacity: .7;
}

.sticky {
  position: sticky;
  top: 0;
}

.hover-shadow {
  transition: all var(--time) ease;
}

.hover-shadow:after {
  content: " ";
  width: 100%;
  height: 100%;
  opacity: 0;
  box-shadow: 0 5px 15px var(--accent-color);
  transition: opacity var(--time) ease;
}

.hover-shadow:hover:after {
  opacity: 1;
}

.hero {
  min-height: 800px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

@media (min-width: 768px) {
  .hero {
    flex-direction: row;
  }
}

.hero__figure {
  min-width: 350px;
  max-width: 500px;
}

.hero__background {
  width: 100%;
  height: 100%;
  filter: blur(48px) opacity(.25);
  position: absolute;
  top: 0;
  left: 0;
}

.hero__content {
  max-width: 450px;
  padding: var(--space-small);
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

.hero__title {
  text-align: left;
  margin-bottom: var(--gap-medium);
  font-size: 4rem;
  line-height: 1;
}

.hero__nav {
  margin-top: var(--gap-medium);
  width: 100%;
}

.hero__nav__list {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.hero__nav__item {
  min-width: 150px;
  text-align: center;
  margin: var(--gap-small) 0;
  display: block;
}

.hero__nav__link {
  padding: var(--gap-small) var(--gap-medium);
  font-weight: 500;
}

.timeline {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.timeline__line {
  width: 2px;
  background: var(--line-color);
  z-index: -1;
  border: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 2.5%;
}

@media (min-width: 768px) {
  .timeline__line {
    left: 50%;
    transform: translateX(-50%);
  }
}

.timeline__time {
  align-self: flex-start;
  font-weight: 500;
}

@media (min-width: 768px) {
  .timeline__time {
    align-self: inherit;
  }
}

.timeline__item {
  width: 100%;
  flex-direction: column;
  display: flex;
}

@media (min-width: 768px) {
  .timeline__item {
    flex-direction: row;
  }
}

.timeline__item__title {
  margin-bottom: var(--gap-small);
