}

.timeline__item__content {
  padding: var(--gap-large);
}

.timeline__item__content a {
  opacity: 1;
}

@media (min-width: 768px) {
  .timeline__item__content a {
    opacity: 0;
    left: 50%;
    bottom: var(--gap-large);
    transition: opacity var(--time) ease;
    position: absolute;
    transform: translateX(-50%);
  }

  .timeline__item__content {
    width: 50%;
    transition: background var(--time) ease;
  }

  .timeline__item__content:hover a {
    opacity: 1;
  }

  .timeline__item aside {
    width: 50%;
  }

  .timeline__item {
    flex-direction: row;
  }

  .timeline__item:nth-of-type(2n+1) {
    text-align: right;
    flex-direction: row-reverse;
  }

  .timeline__item:nth-of-type(2n) {
    flex-direction: row;
  }
}

.exp {
  flex-direction: column;
  display: flex;
}

.exp__item {
  margin: var(--gap-large) 0;
  transform-origin: center;
  transition: var(--time) ease;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow: hidden;
}

@media (min-width: 1200px) {
  .exp__item {
    flex-direction: row;
  }
}

.exp__item:nth-of-type(n) {
  background: linear-gradient(90deg, #00bd561a, #f9fd501a);
}

.exp__item:nth-of-type(2n) {
  background: linear-gradient(90deg, #26baee1a, #9fe8fa);
}

.exp__item:nth-of-type(3n) {
  background: linear-gradient(90deg, #55968f1a, #8acbbb1a);
}

.exp__item__title {
