  height: 400px;
}

@media (min-width: 1200px) {
  .exp__item__figure {
    width: 450px;
    max-height: 550px;
  }

  .exp__item__figure img {
    width: 110%;
    height: 100%;
  }
}

.exp__item__content {
  padding: var(--gap-medium);
  background: var(--accent-color);
  flex-direction: column;
  display: flex;
}

@media (min-width: 1200px) {
  .exp__item__content {
    padding: var(--gap-large);
  }
}

.exp__item__link {
  min-width: 150px;
  margin-top: var(--gap-small);
  padding: var(--gap-small) var(--gap-medium);
  text-align: center;
  align-self: center;
  font-weight: 500;
  display: block;
}

.book {
  grid-template-columns: 1fr 1fr;
  display: grid;
}

@media (min-width: 768px) {
  .book {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.book__cover {
  max-height: 400px;
}

.book__item {
  margin: var(--gap-small);
  text-align: center;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.book__title {
  font-size: 2rem;
}

.note {
  grid-template-columns: 1fr;
  display: grid;
}

@media (min-width: 768px) {
  .note {
    grid-template-columns: 1fr 1fr;
  }
}

.note__item {
  margin: var(--gap-small);
  position: relative;
  overflow: hidden;
}

.note__figure {
  width: 100%;
  height: 300px;
}

.note__title {
  margin: var(--space-small);
  padding: var(--gap-small) var(--gap-medium);
  background: var(--accent-color);
  position: absolute;
  bottom: 0;
  right: 0;
}

.social {
  margin: var(--gap-medium) 0;
  flex-direction: row;
  display: flex;
}

.social__link {
  height: var(--icon);
  width: var(--icon);
  display: inline-block;
}

.social__link svg {
  height: 100%;
  width: 100%;
  fill: var(--white);
}

.social__item {
  margin: 0 var(--gap-small);
  display: block;
}

.toggle {
  width: 80px;
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  transition: all .2s;
  display: flex;
  position: fixed;
  top: 20px;
  right: 20px;
}

.toggle .sun-icon, .toggle .moon-icon {
  width: 30px;
  height: 30px;
  fill: var(--icon-color);
  transition: fill .3s;
}

