  z-index: 1;
  font-size: 32px;
  line-height: 60px;
}

.ball {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin: 7px;
  transition: all .2s;
  position: absolute;
}

.light .icon--light {
  color: #fafafa;
}

.light .icon--dark {
  color: #151d2a;
}

.light .ball, .dark {
  background-color: #151d2a;
}

.dark .toggle {
  background: #151d2a;
  box-shadow: 3px 3px 3px #0e131b, -3px -3px 3px #1c2739;
}

.dark .icon--light {
  color: #fafafa;
}

.dark .icon--dark {
  color: #0e131b;
}

.dark .ball {
  background-color: #fafafa;
  transition: all .3s;
  transform: translateX(60px);
}

